<template>
    <div class="time">
        {{hour}}:{{min}}
    </div>
    <ToSearch></ToSearch>
    <LocalStorage/>
    <RightBox/>
    <AppList/>
    <div class="copyright">
        <a href="https://beian.miit.gov.cn/#/Integrated/index">豫ICP备2021034834号 &nbsp; &nbsp;  </a> |
        <a href="https://blog.csdn.net/qq_53950686"> &nbsp; &nbsp; ©2022 by beink &nbsp; &nbsp;  </a>
  </div>
</template>

<script>
import LocalStorage from './components/LocalStorage.vue';
import ToSearch from './components/ToSearch.vue';
import RightBox from './components/RightBox.vue';
import AppList from './components/AppList.vue';
import {reactive,toRefs,onMounted} from 'vue'
    export default{
        components:{ LocalStorage, ToSearch, RightBox, AppList },
        setup(){
            const vue = reactive({
                hour:new Date().getHours(),
                min: new Date().getMinutes() < 10 ? "0" + new Date().getMinutes() : new Date().getMinutes()
            })
            onMounted(() => {
                time
                function time(){
                    vue.hour = new Date().getHours()
                    vue.min = new Date().getMinutes() < 10 ? "0" + new Date().getMinutes() : new Date().getMinutes()
                }
                setInterval(time,1000)
            })
            return {
                ...toRefs(vue)
            }
        }
    }

</script>

<style scoped>
    
    .time{
        display: flex;
        justify-content: center;
        font-size:40px;
        color:#636e72;
        margin-bottom: 20px;
    }
    .copyright{
        position:absolute;bottom:5px;width:100%;text-align:center
    }
    .copyright a,.copyright{
        font-size:14px;color:#b6b6b6;
        text-decoration: none;
        transition: .3s;
    }
    .copyright a:hover{
        color:#5e5c5c;
    }



</style>
