<template>
    <div class="picture-list">
        <img id="time" src="https://beink.cn/img/moon.png">
        <div class="right-list" v-show="isList">
            <ul> 
                <li v-for="i in list" :key="i.id">
                    <img v-if="i.id==='666'" @click="changeBackground(i.src)" src="../../public/4.jpg" alt="">
                    <img v-else :src="i.src" @click="changeBackground(i.src)" alt="">
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import {reactive,toRefs, watch,onMounted} from 'vue'
    import {nanoid} from 'nanoid'
    export default{
        mounted(){
            let hour = new Date().getHours()
            let Time = document.getElementById("time")
            if(hour<18&&hour>=6)Time.src = "./sun.png"
            else Time.src = "./moon.png"
        },
        setup(){
            const vue  = reactive({
                list:[
                    {id:nanoid(),src:'./41ca469687507ff4e7c6a3c0d3b0ff56.jpg'},
                    {id:nanoid(),src:'./wallhaven-57emd5.jpg'},
                    {id:nanoid(),src:'./1-9.png'},
                    {id:nanoid(),src:'./2-4.png'},
                    {id:'666',src:''}  
                ],
                background:JSON.parse(localStorage.getItem('background')) || ''
            })
            function isList(){
                return false
            }
            function changeBackground(i){
                vue.background =
                document.getElementsByTagName('body')[0].style.backgroundImage=`url('${i}')`
            }
            watch(()=>vue.background,()=>{
                localStorage.setItem('background',JSON.stringify(vue.background))
            })
            onMounted(() => {
                document.getElementsByTagName('body')[0].style.backgroundImage=vue.background
            })
            return{
                ...toRefs(vue),
                isList,
                changeBackground
            }
        }
    }
</script>

<style>
    *{
        padding: 0;
        margin: 0;
    }
    body{
        background-size: 100% 100% ;background-attachment: fixed;
    }
    #time{
        position: absolute;
        right: 0;
        top: 0;
        width: 50px;
    }
    .picture-list{
        position: fixed;
        right: 0;
        top: 0;
    }
    .picture-list:hover .right-list{
        width:314px
    }
    .picture-list .right-list{
        width: 0;
        transition: .5s;
    }
    .picture-list  ul{
        position: relative;
        height: 100vh;
        overflow-y: scroll;
        overflow-x: hidden;
    }
    .picture-list li img{
        position: relative;
        width: 305px;
        height: 173px;
    }
    .picture-list li:nth-child(1){
        margin-top:0;
    }
    .picture-list li{
        list-style: none;
        margin-top:10px ;
        width: 305px;
        height: 173px;
        border: 2px grey solid ;
    }
    .right-list ul::-webkit-scrollbar{
        width:5px;background:#f7f7f7
    }
    .right-list ul::-webkit-scrollbar-thumb{
        border-radius:3px;background:grey
    }
</style>