<template>
    <div class="Search-type" >
        <form action="https://www.baidu.com/baidu" target="_top">
            <input type="text" @focus="hh" @keyup="choise" @blur="name=''" placeholder="......" name="word"  v-model="name">
            <div class="suggest">
              <ul id="search-result" v-show="list.length">
                <li  v-for="i in list" :key="i"  @click="click(i),list=[]">{{i}}</li>
              </ul>
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios'
import { reactive,toRefs,watch} from 'vue'
export default{
    setup() {
      var index = -1
        const vue = reactive({
          name:'',
          list:[]
        })
        function hh(){
          setTimeout(function(){
            axios.get(`https://suggestion.baidu.com/su?wd=${encodeURI(vue.name)}`).then(
              response => {
                  vue.list = []
                  if(vue.name===' ')return 
                  if(response.data===' ')return 
                  var st
                  var en
                  let h = response.data
                  for(let i =0;i<h.length;i++){
                    if(h[i]==='[')st=i
                    if(h[i]==='}')en=i
                  }
                  h = h.slice(st,en)
                  vue.list=JSON.parse(h)
              }, 
              error => {
                  console.log("失败", error.message);
              }
            )
          },150)
        }
        function click(i){
          window.open(`https://www.baidu.com/s?word=${encodeURI(i)}`)
            vue.list=[]
            vue.name=''
        }
        watch(()=>vue.name,()=>{
            hh()
        })
        function choise(e){
          if(e.keyCode===37||e.keyCode===39||e.keyCode===8)index=-1
          let ul = document.getElementsByTagName('ul')[0];
          if(e.keyCode===38){//上
            if(index!==-1)
            ul.children[index].style.backgroundColor=''
            if(index<=0)index=vue.list.length-1 
            else index--
            document.getElementsByTagName('input')[0].value = vue.list[index]
            ul.children[index].style.backgroundColor='#7f7f7f3a'
          }
          if(e.keyCode===40){//下
            if(index!==-1)
            ul.children[index].style.backgroundColor=''
            if(vue.list.length<index+2)index=0
            else index++
            document.getElementsByTagName('input')[0].value = vue.list[index]
            ul.children[index].style.backgroundColor='#7f7f7f3a'
          }
        }
        return {
          ...toRefs(vue),hh,click,choise
        }
    }

  }
</script>

<style scoped>
  *{
    padding:0;
    margin: 0;
  }  
  .Search-type{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  input{
    border:none;
    border-radius: 2%;
    outline: none;
    font-size: 20px;
    line-height: 5px;
    height:40px;
    width: 625px;
    color:#2f3542;
    text-indent: 10px;
    transition: .3s;
    box-shadow: 2px 2px 10px #d8dee9;
  }
  input:hover{
    box-shadow: 2px 2px 10px#b4b9c4;
  }
  .suggest{
    position:relative;
  }
  ul{
    position: absolute;
    z-index: 99999;
    width: 100%;
    background-color: white;
    box-shadow: 7px 8px 13px 0px #c3c6cd;
  }
  li{
    position: relative;
    list-style: none;
    font-size:15px;
    line-height:30px;
    height: 30px;
    padding-left:5px;
    transition: .2s;
  }
  li:hover{
    background-color: #7f7f7f3a;
  }
  
</style>